/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC, useEffect, useState} from 'react'

import {Link, withRouter} from 'react-router-dom'
import {
  message,
  Checkbox,
  Skeleton,
  Card,
  Modal,
  Row,
  Col,
  Form,
  Select,
  InputNumber,
  Input,
  Button,
  Spin,
} from 'antd'
import {KTSVG} from '../../../_metronic/helpers'
import moment from 'moment'
import PapaParse from 'papaparse'
import 'react-datepicker/dist/react-datepicker.css'
import {PlusOutlined} from '@ant-design/icons'
import {mainConfig} from '../../../config/mainConfig'
import Helper from '../../utils/Helper'

const {Option} = Select

const collator = new Intl.Collator(undefined, {
  numeric: true,
  sensitivity: 'base',
})
let arrIndex = 0,
  token = ''

const POItemsWrapper = (props: any) => {
  let multiselectRef = React.useRef<any>()

  const [loading, setloading] = useState<any>(false)
  const [POArr, setPOArr] = useState<any[]>([])

  const [PoModal, setPoModal] = useState<any>(false)
  const [RaisePoModal, setRaisePoModal] = useState<any>(false)
  const [poModalLoader, setpoModalLoader] = useState<any>(false)

  const [AllPOs, setAllPOs] = useState<any[]>([])
  const [AllPOsTable, setAllPOsTable] = useState<any[]>([])
  const [AllPOHistorical, setAllPOHistorical] = useState<any[]>([])

  const [tabSelected, settabSelected] = useState<any>('Latest')
  const [AllPOsCSV, setAllPOsCSV] = useState<any[]>([])

  const [AC4, setAC4] = useState<any[]>([])
  const [Supplier, setSupplier] = useState<any[]>([])
  const [AC4List, setAC4List] = useState<any[]>([])
  const [SupplierList, setSupplierList] = useState<any[]>([])

  const [ChildProducts, setChildProducts] = useState<any[]>([])
  const [SuppListCSV, setSuppListCSV] = useState<any[]>([])
  const [StatusListCSV, setStatusListCSV] = useState<any[]>([])
  const [ProdListCSV, setProdListCSV] = useState<any[]>([])
  const [AllPOSelected, setAllPOSelected] = useState<any[]>([])

  const [poSuppIdSelect, setpoSuppIdSelect] = useState<any[]>([])
  const [poProdIdSelect, setpoProdIdSelect] = useState<any[]>([])
  const [poaggcodeSelect, setpoaggcodeSelect] = useState<any[]>([])

  const [ErrQty, setErrQty] = useState<any>(false)
  const [ErrPrice, setErrPrice] = useState<any>(false)
  const [ErrProdId, setErrProdId] = useState<any>(false)
  const [ErrSuppId, setErrSuppId] = useState<any>(false)
  const [Erraggcode, setErraggCode] = useState<any>(false)

  const [poNote, setpoNote] = useState<any>('')
  const [poQty, setpoQty] = useState<any>('')
  const [poPrice, setpoPrice] = useState<any>('')
  const [poaggcode, setpoaggcode] = useState<any>('')
  const [poProdId, setpoProdId] = useState<any>('')
  const [poSuppId, setpoSuppId] = useState<any>('')

  const [CsvSupp, setCsvSupp] = useState<any[]>([])
  const [CsvProd, setCsvProd] = useState<any[]>([])
  const [CsvStatus, setCsvStatus] = useState<any[]>([])
  const [CsvStartDate, setCsvStartDate] = useState<any>('')
  const [CsvEndDate, setCsvEndDate] = useState<any>('')
  const [FilterDate, setFilterDate] = useState<any>('')
  const [POPrice, setPOPrice] = useState<any>('Asc')
  const [POQty, setPOQty] = useState<any>('Asc')
  const [POsortSCflag, setPOsortSCflag] = useState<any>('Asc')
  const [POsortPCflag, setPOsortPCflag] = useState<any>('Asc')
  const [POsortPDflag, setPOsortPDflag] = useState<any>('Asc')
  const [messageRaisePo, setmessageRaisePo] = useState<any>(false)

  useEffect(() => {
    setloading(true)
    setAllPOSelected([])
    getAllPO()
    getAc4Supplier()
  }, [])

  const getAllPO = () => {
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/poitems`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }
          response.json().then(function (data) {
            setAllPOs(data.data)
            setAllPOsTable(data.data)
            createData(data.data)
            setloading(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      setloading(false)
    }
  }

  const createData = (data: any) => {
    let arr = new Array(),
      arr1 = new Array(),
      arr2 = new Array(),
      arr3 = new Array()

    data.map((i: any) => {
      let obj = {
        PO_ID: i.po_id,
        Supplier: i.supplier,
        ProductCode: i.product_code,
        Quantity: i.quantity,
        Price: i.price,
        Notes: i.notes,
      }

      if (arr1.length <= 0) {
        let obj = {value: i.supplier, label: i.supplier}
        arr1.push(obj)
      } else {
        let found = arr1.find((p) => p.supplier == i.supplier)
        if (found == undefined) {
          let obj = {value: i.supplier, label: i.supplier}
          arr1.push(obj)
        }
      }

      if (arr2.length <= 0) {
        arr2.push({id: i.status, status: i.status == 0 ? 'Pending' : 'Approved'})
      } else {
        let found = arr2.find((p) => p.id == i.status)
        if (found == undefined) {
          arr2.push({id: i.status, status: i.status == 0 ? 'Pending' : 'Approved'})
        }
      }

      if (arr3.length <= 0) {
        arr3.push({value: i.product_code, label: i.product_code})
      } else {
        let found = arr3.find((p) => p.product_code == i.product_code)
        if (found == undefined) {
          arr3.push({value: i.product_code, label: i.product_code})
        }
      }

      arr.push(obj)
    })
    setAllPOsCSV(arr)
    setSuppListCSV(arr1)
    setStatusListCSV(arr2)
    setProdListCSV(arr3)
  }

  const getAc4Supplier = () => {
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-ac-products`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            return
          }
          response.json().then(function (data) {
            let tempArr = new Array()

            data.data.ac4.map((i: any) => {
              let obj = {
                value: `${i.parent_product_code} - ${i.desc}`,
                label: `${i.parent_product_code} - ${i.desc}`,
              }
              tempArr.push(obj)
            })

            setAC4(tempArr)

            setAC4List(tempArr)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {}
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-suppliers`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            return
          }
          response.json().then(function (data) {
            let tempArr1 = new Array(),
              tempArr2 = new Array()

            data.data.supplier.map((i: any) => {
              let obj = {value: i.supplier, label: i.supplier, key: i.id}
              tempArr1.push(obj)
            })
            data.data.supplier.map((i: any) => {
              let obj = {value: i.id, label: i.supplier}
              tempArr2.push(obj)
            })

            setSupplier(tempArr1)

            setSupplierList(tempArr2)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {}
  }

  const onSearchPO = (e: any) => {
    let n = e.target.value
    let searchdata = new Array()

    AllPOs.map((i) => {
      if (
        i.supplier.toLowerCase().includes(n.toLowerCase()) ||
        i.product_code.toLowerCase().includes(n.toLowerCase())
      ) {
        searchdata.push(i)
      }
    })

    setAllPOs(searchdata)

    if (n == '') {
      setAllPOs(AllPOsTable)
    }
  }

  const onSubmit = (values: any) => {
    let suppCode = new Array()

    values.suppCode.map((d: any) => {
      SupplierList.map((a, s) => {
        if (a.label == d) {
          suppCode.push(a.value)
        }
      })
    })

    let arr = new Array()

    POArr.map((d, i) => {
      let obj = {
        product_id: `${values.prodCode[i]}`,
        supplier_id: `${suppCode[i]}`,
        quantity: `${values.qty[i]}`,
        price: `${values.price[i]}`,
      }

      arr.push(obj)
    })
    let obj1 = {
      poitems: arr,
    }

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/create-po-items`, {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(obj1),
      })
        
        .then(function (data) {
          data.json().then(function (data) {
            if (data) {
              setloading(false)
              setpoQty('')
              setpoPrice('')
              setpoNote('')
              getAllPO()
              setPoModal(false)
              arrIndex = 0
              setPOArr([])
              message.success(data.message)
              getAllPO()
            }
          })
          if (data.status == 204) {
            getAllPO()
            setpoQty('')
            setpoPrice('')
            setpoNote('')
          }
        })
        .catch(function (error) {
          getAllPO()
          setloading(false)
        })
    } catch (err) {}
  }

  const createPO = () => {
    let poItems = new Array()

    AllPOSelected.map((d, i) => {
      poItems.push(d.po_item_id)
    })

    let obj1 = {
      poitems: poItems,
    }

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/raise-po`, {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(obj1),
      })
        .then(function (data) {
          data.json().then(function (data) {
            if (data.success == true) {
              setRaisePoModal(false)
              message.success(data.message)

              getAllPO()
              setAllPOSelected([])
            } else {
              message.error(data.message)
              setAllPOSelected([])
              getAllPO()
            }
          })
        })
        .catch(function (error) {
          setloading(false)
        })
    } catch (err) {}
  }

  const onSelectAg = (selectedItems: any) => {
    let tempAg = selectedItems.split('-')[0]
    setpoaggcode(tempAg)
    setpoaggcodeSelect(tempAg)
    setErraggCode(false)
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-product-codes/${tempAg}`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            return
          }

          response.json().then(function (data) {
            let tempArr = new Array()

            data.data.map((i: any) => {
              let obj = {value: i.prod_id, label: i.product_code, key: i.prod_id}
              tempArr.push(obj)
            })
            setChildProducts(tempArr)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {}
  }

  const onSelectProd = (selectedItems: any, i: any) => {
    setpoProdId(selectedItems.value)
    setpoProdIdSelect(selectedItems)
    setErrProdId(false)
  }

  const onSelectSupp = (selectedItems: any) => {
    setpoSuppId(selectedItems.value)
    setpoSuppIdSelect(selectedItems)
    setErrSuppId(false)
  }

  const onSelectCsvSupp = (selectedItems: any) => {
    setCsvSupp([selectedItems])
  }

  const onSelectCsvProd = (selectedItems: any) => {
    setCsvProd([selectedItems])
  }

  const onSelectCsvStatus = (selectedItems: any) => {
    setCsvStatus(selectedItems)
  }

  const OnModalClose = () => {
    setpoQty('')
    setpoPrice('')
    setpoNote('')
    setpoSuppId('')
    setpoProdId('')
    setpoaggcode('')
    setCsvStartDate('')
    setCsvEndDate('')
    setpoaggcodeSelect([])
    setpoProdIdSelect([])
    setpoSuppIdSelect([])

    setCsvSupp([])
    setCsvStatus([])
    setCsvProd([])

    setErrQty(false)
    setErrPrice(false)
    setErrProdId(false)
    setErrSuppId(false)

    arrIndex = 1
    let temp = new Array()
    temp.push(arrIndex)
    setPOArr(temp)
  }

  async function downloadCsv(e: any) {
    e.preventDefault()
    let arr = new Array(),
      arr1 = new Array(),
      arr2 = new Array(),
      arr3 = new Array(),
      tempData = new Array()
    if (tabSelected == 'Latest') {
      tempData = AllPOSelected.length > 0 ? AllPOSelected : AllPOs
    }
    if (tabSelected == 'Historical') {
      tempData = AllPOHistorical
    }

    if (CsvSupp.length > 0) {
      tempData.map((a) => {
        CsvSupp.map((b) => {
          if (b.value == a.supplier) {
            arr.push(a)
          }
        })
      })
    }

    if (CsvProd.length > 0) {
      let temp = CsvSupp.length > 0 ? arr : tempData
      temp.map((a) => {
        CsvProd.map((b) => {
          if (b.value == a.product_code) {
            arr1.push(a)
          }
        })
      })
    }

    if (CsvStatus.length > 0) {
      let temp = CsvProd.length > 0 ? arr1 : CsvSupp.length > 0 ? arr : tempData
      temp.map((a) => {
        CsvStatus.map((b) => {
          if (b.id == a.status) {
            arr2.push(a)
          }
        })
      })
    }

    if (CsvStartDate != '' || CsvEndDate != '') {
      let tempArr = new Array(),
        tempArr1 = new Array()

      let temp =
        CsvStatus.length > 0
          ? arr2
          : CsvProd.length > 0
          ? arr1
          : CsvSupp.length > 0
          ? arr
          : tempData

      if (CsvStartDate != '') {
        temp.map((a) => {
          if (
            moment(CsvStartDate).format('DD/MM/YYYY') < moment(a.raised_on).format('DD/MM/YYYY')
          ) {
            tempArr.push(a)
          }
        })
      }

      if (CsvEndDate != '') {
        let temp1 =
          CsvStartDate != ''
            ? tempArr
            : CsvProd.length > 0
            ? arr1
            : CsvSupp.length > 0
            ? arr
            : tempData

        temp1.map((a) => {
          if (moment(CsvEndDate).format('DD/MM/YYYY') > moment(a.raised_on).format('DD/MM/YYYY')) {
            tempArr1.push(a)
          }
        })
      }

      arr3 = tempArr.length > 0 ? tempArr : tempArr1.length > 0 ? tempArr1 : []
    }

    await new Promise((resolve, reject) => setTimeout(resolve, 3000))

    var data = {
      fields: ['po_id', 'supplier', 'product_code', 'quantity', 'price', 'notes'],
      data:
        arr3.length > 0
          ? arr3
          : arr2.length > 0
          ? arr2
          : arr1.length > 0
          ? arr1
          : arr.length > 0
          ? arr
          : tempData.length > 0
          ? tempData
          : [],
    }
    var csv = PapaParse.unparse(data)

    var csvData = new Blob([csv], {type: 'text/csv;charset=utf-8;'})
    var csvURL = null

    csvURL = window.URL.createObjectURL(csvData)

    var tempLink = document.createElement('a')
    tempLink.href = csvURL
    tempLink.setAttribute('download', `PO.csv`)
    tempLink.click()

    $('#csv').modal('hide')

    setCsvStartDate('')
    setCsvEndDate('')
    setCsvSupp([])
    setCsvStatus([])
    setCsvProd([])
    multiselectRef.current.resetSelectedValues()
  }

  const onSortArr = (data: any) => {
    switch (data) {
      case 'Price':
        if (POPrice == 'Asc') {
          setPOPrice('Desc')
          let tempArr = AllPOs.sort((a: any, b: any) => parseFloat(a.price) - parseFloat(b.price))
          setAllPOs(tempArr)
        }

        if (POPrice == 'Desc') {
          setPOPrice('Asc')
          let tempArr = AllPOs.sort((a: any, b: any) => parseFloat(b.price) - parseFloat(a.price))
          setAllPOs(tempArr)
        }

        break
      case 'Qty':
        if (POQty == 'Asc') {
          setPOQty('Desc')
          let tempArr = AllPOs.sort(
            (a: any, b: any) => parseFloat(a.quantity) - parseFloat(b.quantity)
          )
          setAllPOs(tempArr)
        }

        if (POQty == 'Desc') {
          setPOQty('Asc')
          let tempArr = AllPOs.sort(
            (a: any, b: any) => parseFloat(b.quantity) - parseFloat(a.quantity)
          )
          setAllPOs(tempArr)
        }
        break
      case 'Sc':
        if (POsortSCflag == 'Asc') {
          setPOsortSCflag('Desc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(a.supplier, b.supplier)
          })
          setAllPOs(sorted)
        }

        if (POsortSCflag == 'Desc') {
          setPOsortSCflag('Asc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(b.supplier, a.supplier)
          })
          setAllPOs(sorted)
        }
        break

      case 'ProductCode':
        if (POsortPCflag == 'Asc') {
          setPOsortPCflag('Desc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(a.product_code, b.product_code)
          })
          setAllPOs(sorted)
        }

        if (POsortPCflag == 'Desc') {
          setPOsortPCflag('Asc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(b.product_code, a.product_code)
          })
          setAllPOs(sorted)
        }
        break

      case 'ProductDescription':
        if (POsortPDflag == 'Asc') {
          setPOsortPDflag('Desc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(a.product_desc, b.product_desc)
          })
          setAllPOs(sorted)
        }

        if (POsortPDflag == 'Desc') {
          setPOsortPDflag('Asc')

          const sorted = AllPOs.sort((a, b) => {
            return collator.compare(b.product_desc, a.product_desc)
          })
          setAllPOs(sorted)
        }
        break
      default:
    }
  }
  const onSelectCheckbox = (data: any) => {
    let dataArr = new Array()

    dataArr = [...AllPOSelected]

    if (dataArr.length <= 0) {
      dataArr.push(data)
    } else {
      let index = dataArr.findIndex((item) => item.po_item_id == data.po_item_id)

      if (index == -1) {
        dataArr.push(data)
      } else {
        dataArr.splice(index, 1)
      }
    }
    setAllPOSelected(dataArr)
  }

  const onSelectCheckboxAll = () => {
    let dataArr = AllPOs
    if (AllPOSelected.length == AllPOs.length) {
      setAllPOSelected([])
    } else {
      setAllPOSelected(dataArr)
    }
  }

  const ondateSelect = (date: any) => {
    let tempDate = moment(date).format('DD/MM/YYYY')

    setFilterDate(date)

    let searchdata = new Array()

    if (tabSelected == 'Latest') {
      if (tempDate != 'Invalid date') {
        AllPOs.map((i) => {
          if (moment(i.raised_on).format('DD/MM/YYYY') == tempDate) {
            searchdata.push(i)
          }
        })

        if (searchdata.length > 0) {
          setAllPOs(searchdata)
        }
      } else {
        setAllPOs(AllPOsTable)
      }
    }
    if (tabSelected == 'Historical') {
      if (tempDate != 'Invalid date') {
        AllPOHistorical.map((i) => {
          if (moment(i.raised_on).format('DD/MM/YYYY') == tempDate) {
            searchdata.push(i)
          }
        })
        if (searchdata.length > 0) {
          setAllPOs(searchdata)
        }
      } else {
        setAllPOs(AllPOsTable)
      }
    }
  }

  function add() {
    setpoModalLoader(true)

    let arr = new Array(),
      arr1 = new Array()

    arr = POArr
    arr.push(arrIndex)
    setPOArr(arr)

    arrIndex++
    setTimeout(function () {
      setpoModalLoader(false)
    }, 100)
  }


  function remove(i: any) {
    setpoModalLoader(true)
    let tempArr = new Array(),
      tempArr1 = new Array()

    tempArr = POArr
    tempArr.map((d, index) => {
      if (index == i) {
        tempArr.splice(index, 1)
        // tempArr1.splice(index,1)
      }
    })

    setTimeout(function () {
      setpoModalLoader(false)
    }, 100)
    return

    arrIndex++

    let arr = new Array()

    arr = POArr

    arr.push(arrIndex)

    setPOArr(arr)
  }



  function openModal() {
    setpoModalLoader(true)
    if (arrIndex == 0) {
      const po = {
        id: arrIndex,
        product_id: poProdId,
        supplier_id: poSuppId,
        price: poPrice,
        quantity: poQty,
      }
      let arr1 = new Array()
      POArr.push(arrIndex)

      arrIndex++
    }
    setPoModal(true)
    setTimeout(function () {
      setpoModalLoader(false)
    }, 100)
  }



  function OnPOModalClose() {
    setErrQty(false)
    setErrPrice(false)
    setErrProdId(false)
    setErrSuppId(false)
    setPoModal(false)
    arrIndex = 0
    setPOArr([])
  }


  const onDownloadCsv = (po: any) => {
    let tempData = new Array()

    AllPOSelected.map((p) => {
      let obj = {
        PO_Number: po,
        supplier: p.supplier,
        product_code: p.product_code,
        quantity: p.quantity,
        price: p.price,
      }
      tempData.push(obj)
    })

    var data = {
      fields: ['PO_Number', 'supplier', 'product_code', 'quantity', 'price'],
      data: tempData.length > 0 ? tempData : [],
    }
    var csv = PapaParse.unparse(data)

    var csvData = new Blob([csv], {type: 'text/csv;charset=utf-8;'})
    var csvURL = null

    csvURL = window.URL.createObjectURL(csvData)

    var tempLink = document.createElement('a')
    tempLink.href = csvURL
    tempLink.setAttribute('download', `${po}.csv`)
    tempLink.click()
    setAllPOSelected([])

    
  }

  const onSearchSup = (val: any) => {
    let tempArr = new Array()

    if (val != '' && val.length >= 3) {
      Supplier.map((i) => {
        if (i.label.toLowerCase().includes(val.toLowerCase())) {
          tempArr.push(i)
        }
      })

      setSupplier(tempArr)
    } else {
      setSupplier(SupplierList)
    }
  }

  const raisePo = () => {
    let poItems = new Array()

    AllPOSelected.map((d, i) => {
      poItems.push(d.po_item_id)
    })

    let obj1 = {
      poitems: poItems,
    }

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/check-po-exists`, {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(obj1),
      })
        
        .then(function (data) {
          data.json().then(function (data) {
            if (data.success == true) {
              if (data.data) {
                setmessageRaisePo(true)
              } else {
                setmessageRaisePo(false)
              }
            } else {
            }
          })
        })
        .catch(function (error) {
          setloading(false)
        })
    } catch (err) {}
    setTimeout(function () {
      setRaisePoModal(true)
    }, 700)
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <React.Fragment>
      {loading == true ? (
        <React.Fragment>
          <Skeleton loading={loading} active></Skeleton>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Row>
            <Col xs={12} sm={12} md={20} lg={20} xl={20}>
              <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
                <Link to='/home' className='pe-1 cursor-pointer'>
                  <li className='breadcrumb-item pe-1'>Home</li>
                </Link>
                / &nbsp;<li className='breadcrumb-item pe-3'>PO Items</li>
              </ol>
            </Col>

            <Col xs={12} sm={12} md={4} lg={4} xl={4}>
              <Link to={{pathname: '/poList'}}>
                <button className='btn btn-sm btn-primary '>Go To PO List</button>
              </Link>
            </Col>
          </Row>

          <div className='row g-5 gx-xxl-8' style={{marginTop: '1%'}}>
            <div className='col-xxl-12'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2'>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          PO Items
                        </span>
                      </h3>
                      <div className='d-none d-sm-none d-md-block'>
                        <div className='card-toolbar'>
                          <input
                            type='search'
                            onChange={onSearchPO}
                            className='form-control form-control-solid'
                            placeholder='Search by Supplier / Prod Code'
                          />
                        </div>
                      </div>
                      <div className='d-sm-block d-md-none'>
                        <div className='card-toolbar' style={{width: '100%'}}>
                          <input
                            type='search'
                            onChange={onSearchPO}
                            className='form-control form-control-solid'
                            placeholder='Search by Supplier / Prod Code'
                          />
                        </div>
                      </div>

                      <div
                        className='card-toolbar'
                        data-bs-toggle='tooltip'
                        data-bs-placement='top'
                        data-bs-trigger='hover'
                        title='Click to Create New Item'
                      >
                        <button
                          type='button'
                          className='btn btn-sm btn-primary'
                          onClick={() => {
                            openModal()
                          }}
                        >
                          <KTSVG
                            path='/media/icons/duotune/arrows/arr075.svg'
                            className='svg-icon-3'
                          />
                          Create POItem
                        </button>
                      </div>

                      <div
                        className='card-toolbar'
                        data-bs-toggle='tooltip'
                        data-bs-placement='top'
                        data-bs-trigger='hover'
                        title='Click to Raise PO'
                      >
                        <button
                          disabled={AllPOSelected.length == 0 ? true : false}
                          type='button'
                          className='btn btn-sm btn-primary'
                          onClick={raisePo}
                        >
                          <KTSVG
                            path='/media/icons/duotune/arrows/arr075.svg'
                            className='svg-icon-3'
                          />
                          Raise PO
                        </button>
                      </div>
                    </div>

                    <div className='card-body py-3 '>
                      <div className='table-responsive h-550px table-scroll'>
                        <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                          <thead>
                            <tr className='fw-bolder text-muted'>
                              <th className='w-20px'>
                                <Checkbox onChange={() => onSelectCheckboxAll()} />
                              </th>

                              <th className='min-w-100px' onClick={() => onSortArr('Sc')}>
                                Supplier
                                {POsortSCflag != 'Desc' ? (
                                  <React.Fragment>
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr062.svg'}
                                    />
                                  </React.Fragment>
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th className='min-w-100px' onClick={() => onSortArr('ProductCode')}>
                                Product Code
                                {POsortPCflag != 'Desc' ? (
                                  <React.Fragment>
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr062.svg'}
                                    />
                                  </React.Fragment>
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-100px'
                                onClick={() => onSortArr('ProductDescription')}
                              >
                                product Desc
                                {POsortPDflag != 'Desc' ? (
                                  <React.Fragment>
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr062.svg'}
                                    />
                                  </React.Fragment>
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-70px cursor-pointer'
                                onClick={() => onSortArr('Qty')}
                              >
                                Quantity
                                {POQty != 'Desc' ? (
                                  <React.Fragment>
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr062.svg'}
                                    />
                                  </React.Fragment>
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-70px cursor-pointer'
                                onClick={() => onSortArr('Price')}
                              >
                                Price
                                {POPrice != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary cursor-pointer'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                            </tr>
                          </thead>

                          <tbody>
                            {AllPOs != undefined &&
                              AllPOs.map((j: any) => {
                                return (
                                  <tr>
                                    <td>
                                      <Checkbox
                                        onChange={() => onSelectCheckbox(j)}
                                        checked={
                                          AllPOSelected.findIndex(
                                            (item) => item.po_item_id == j.po_item_id
                                          ) != -1
                                        }
                                      />
                                    </td>

                                    <td>
                                      <Link
                                        to={{pathname: '/supplier', state: {id: j.supplier_id}}}
                                      >
                                        <span className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer text-decoration-underline link'>
                                          {j.supplier}
                                        </span>
                                      </Link>
                                    </td>
                                    <td>
                                      <Link
                                        to={{
                                          pathname: '/product',
                                          state: {
                                            prod_id: j.prod_id,
                                          },
                                        }}
                                      >
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.product_code ? j.product_code : '-'}
                                        </span>
                                      </Link>
                                    </td>
                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.desc}
                                      </span>
                                    </td>
                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.quantity}
                                      </span>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {Helper.addZero(j.price)}
                                      </span>
                                    </td>
                                  </tr>
                                )
                              })}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <Modal
            title='Create PO Item'
            centered
            destroyOnClose={true}
            visible={PoModal}
            onOk={OnPOModalClose}
            onCancel={OnPOModalClose}
            footer={null}
            width={1080}
          >
            <Form onFinish={onSubmit} layout='vertical'>
              <div className='row'>
                {POArr.map((a, i) => {
                  return (
                    <div className='col-md-6'>
                      <Spin spinning={poModalLoader}>
                        <Col span={24} offset={1}>
                          <Card style={{borderRadius: 10, marginTop: '2%'}}>
                            <Row>
                              <Col span={22}>
                                <Row>
                                  <Col span={24}>
                                    <Form.Item
                                      name={['aggCode', i]}
                                      label='Aggregate Code'
                                      rules={[
                                        {
                                          required: true,
                                          message: 'Please select aggregate code',
                                        },
                                      ]}
                                    >
                                      <Select
                                        placeholder='Please Select Code'
                                        onChange={(e) => onSelectAg(e)}
                                        showSearch
                                        optionFilterProp='children'
                                      >
                                        {AC4.map((c) => {
                                          return (
                                            <Option key={c.value} value={c.value}>
                                              {c.label}
                                            </Option>
                                          )
                                        })}
                                      </Select>
                                    </Form.Item>
                                  </Col>
                                </Row>

                                <Row>
                                  <Col span={11}>
                                    <Form.Item
                                      name={['prodCode', i]}
                                      label='Product Code'
                                      rules={[
                                        {
                                          required: true,
                                          message: 'Please select product code',
                                        },
                                      ]}
                                    >
                                      <Select
                                        placeholder='Please Select Code'
                                        showSearch
                                        optionFilterProp='children'
                                      >
                                        {ChildProducts.map((c) => {
                                          return (
                                            <Option key={c.value} value={c.value}>
                                              {c.label}
                                            </Option>
                                          )
                                        })}
                                      </Select>
                                    </Form.Item>
                                  </Col>

                                  <Col span={1}></Col>
                                  <Col span={11}>
                                    <Form.Item
                                      name={['suppCode', i]}
                                      label='Supplier Code'
                                      rules={[
                                        {
                                          required: true,
                                          message: 'Please select supplier code',
                                        },
                                      ]}
                                    >
                                      <Select placeholder='Please Select Code' showSearch>
                                        {Supplier &&
                                          Supplier.map((c) => {
                                            return (
                                              <Option key={c.value} value={c.value}>
                                                {c.label}
                                              </Option>
                                            )
                                          })}
                                      </Select>
                                    </Form.Item>
                                  </Col>
                                </Row>

                                <Row>
                                  <Col span={11}>
                                    <Form.Item
                                      name={['qty', i]}
                                      label='Quantity'
                                      rules={[
                                        {
                                          required: true,
                                          message: 'Please enter quantity',
                                        },
                                        {
                                          pattern: new RegExp(/^[0-9]+$/),
                                          message: "value is in number only",
                                        },
                                      ]}
                                    >
                                      <Input min={1} style={{width: '100%'}} />
                                    </Form.Item>
                                  </Col>

                                  <Col span={1}></Col>

                                  <Col span={11}>
                                    <Form.Item
                                      name={['price', i]}
                                      label='Price'
                                      rules={[
                                        {
                                          required: true,
                                          message: 'Please enter price',
                                        },
                                        {
                                          pattern: new RegExp(/^[0-9,.,0-9]+$/),
                                          message: "value is in number only",
                                        },
                                      ]}
                                    >
                                      <InputNumber min={0} style={{width: '100%'}} />
                                    </Form.Item>
                                  </Col>
                                </Row>
                              </Col>
                              <Col span={1}></Col>

                              <Col span={1} style={{marginTop: '23%'}}>
                                {i != 0 && (
                                  <Button shape='circle' onClick={() => remove(i)}>
                                    <KTSVG
                                      path='/media/icons/duotune/arrows/arr090.svg'
                                      className='svg-icon-3'
                                    />{' '}
                                  </Button>
                                )}
                              </Col>
                            </Row>
                          </Card>
                        </Col>
                      </Spin>
                    </div>
                  )
                })}
              </div>
              <Row>
                <Button
                  type='primary'
                  shape='circle'
                  style={{margin: '2%'}}
                  onClick={() => add()}
                  icon={<PlusOutlined />}
                />{' '}
              </Row>

              <Row>
                <Button type='primary' htmlType='submit' style={{borderRadius: '5px'}}>
                  {' '}
                  Submit{' '}
                </Button>
              </Row>
            </Form>
          </Modal>

          <Modal
            title={
              messageRaisePo == true ? (
                <React.Fragment>
                  <Row>
                    <Col span={24}>
                      {' '}
                      Raise Po <br></br>Po items already present in another POs
                      <>
                        <Link
                          to={{
                            pathname: '/recent-po',
                          }}
                          target='_blank'
                          className='m-2 cursor-pointer'
                        >
                          <a className='btn btn-link btn-color-primary btn-active-color-info me-5 mb-2'>
                            View More
                          </a>
                        </Link>
                      </>
                    </Col>
                  </Row>
                </React.Fragment>
              ) : (
                'Raise Po'
              )
            }
            centered
            destroyOnClose={true}
            visible={RaisePoModal}
            onOk={() => setRaisePoModal(false)}
            onCancel={() => setRaisePoModal(false)}
            footer={null}
            width={600}
            style={{top: 2, marginLeft: '30%'}}
          >
            <Card
              style={{
                padding: '-5px',
                borderRadius: '10px',
                marginBottom: '5px',
                backgroundColor: '#F5F5F5',
                fontWeight: 'bold',
              }}
            >
              <Row>
                <Col span={3}>No.</Col>
                <Col span={7}>Supplier</Col>
                <Col span={7}>Prod_Code</Col>
                <Col span={4}>Qty</Col>
                <Col span={3}>Price</Col>
              </Row>
            </Card>
            {AllPOSelected.length != 0 &&
              AllPOSelected.map((d, i) => {
                return (
                  <Card
                    style={{
                      padding: '-5px',
                      borderRadius: '10px',
                      marginBottom: '5px',
                      backgroundColor: '#F5F5F5',
                    }}
                  >
                    <Row>
                      <Col span={3}>{i + 1}</Col>
                      <Col span={7}>{d.supplier}</Col>
                      <Col span={7}>{d.product_code}</Col>
                      <Col span={4}>{d.quantity}</Col>
                      <Col span={3}>{d.price}</Col>
                    </Row>
                  </Card>
                )
              })}

            <Row style={{marginTop: '3%'}}>
              <Col span={4}>
                <Button type='primary' onClick={createPO} style={{borderRadius: '5px'}}>
                  {' '}
                  Submit{' '}
                </Button>
              </Col>
            </Row>
          </Modal>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(POItemsWrapper)
